.App{
  min-height:100vh;
  background-image: linear-gradient(to top, #30cfd0 0%, #330867 100%);
}


.search-form{
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-bar{
  width: 50%;
  border: none;
  padding: 10px;;
}
.search-button{
  background-color: rgb(10, 112, 1);
  border: none;
  padding: 10px 20px;
  color: white;
  transition: .3s ease-in-out;
}
.search-button:hover{
  background-color: yellow;
  color: black;
  font-size: 15px;
}

.recipes{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}